import { createContext, useEffect, useState } from "react";
import axios from "axios";
export let cartContext = createContext();

export function CartContextProvider(props) {
  let headers = {
    token: localStorage.getItem("userToken"),
  };
  const [cartId, setcartId] = useState(null);
  const [cartItemsNumber, setcartItemsNumber] = useState(0);

  async function getCart() {
    let response = await getLoggedUserCart();
    if (response?.data?.status === "success") {
      setcartId(response.data.data._id);
      setcartItemsNumber(response.data.numOfCartItems);
    }
  }
  useEffect(() => {
    getCart();
  }, []);

  async function addToCart(productIdNumber) {
    return axios
      .post(
        `https://route-ecommerce.onrender.com/api/v1/cart`,
        {
          productId: productIdNumber,
        },
        {
          headers: headers,
        }
      )
      .then((response) => response)
      .catch((error) => error);
  }
  async function getLoggedUserCart() {
    return axios
      .get(`https://route-ecommerce.onrender.com/api/v1/cart`, {
        headers: headers,
      })
      .then((response) => response)
      .catch((error) => error);
  }
  async function removeItem(productId) {
    return axios
      .delete(`https://route-ecommerce.onrender.com/api/v1/cart/${productId}`, {
        headers: headers,
      })
      .then((response) => response)
      .catch((error) => error);
  }
  async function updateCartItemQuantity(productId, countNumber) {
    return axios
      .put(
        `https://route-ecommerce.onrender.com/api/v1/cart/${productId}`,
        {
          count: countNumber,
        },
        {
          headers: headers,
        }
      )
      .then((response) => response)
      .catch((error) => error);
  }

  async function onlinePayment(cartId, shippingAddress) {
    return axios
      .post(
        `https://route-ecommerce.onrender.com/api/v1/orders/checkout-session/${cartId}?url=http://localhost:3000`,
        {
          shippingAddress: shippingAddress,
        },
        {
          headers: headers,
        }
      )
      .then((response) => response)
      .catch((error) => error);
  }
  return (
    <cartContext.Provider
      value={{
        cartId,
        cartItemsNumber,
        setcartItemsNumber,
        addToCart,
        getLoggedUserCart,
        removeItem,
        updateCartItemQuantity,
        onlinePayment,
      }}
    >
      {props.children}
    </cartContext.Provider>
  );
}
