import React, { useContext, useEffect, useState } from "react";
import { cartContext } from "../../Context/CartContext";
import { toast } from "react-hot-toast";
import { Link } from "react-router-dom";
import { Helmet } from "react-helmet";
export default function Cart() {
  const [cartDetails, setcartDetails] = useState(null);
  let { getLoggedUserCart, removeItem, updateCartItemQuantity } =
    useContext(cartContext);

  async function getCartData() {
    let response = await getLoggedUserCart()
      .then((res) => res)
      .catch((error) => {
        // console.log(error);
      });

    if (response?.data?.status === "success") {
      setcartDetails(response.data.data);
      // console.log(response.data.data);
    }
  }
  async function deleteItem(productId) {
    let response = await removeItem(productId);
    if (response?.data?.status === "success") {
      setcartDetails(response.data.data);
      toast.success("product removed successfully");
    }
  }
  async function updateItemCount(productId, count) {
    let response = await updateCartItemQuantity(productId, count);
    if (response?.data?.status === "success") {
      setcartDetails(response.data.data);
      toast.success("product updated successfully");
    }
  }
  useEffect(() => {
    getCartData();
  }, []);
  return (
    <>
      <Helmet>
        <title>Cart</title>
      </Helmet>
      {cartDetails ? (
        <div className="row p-4 my-4">
          <h3>Shop Cart</h3>
          <h6 className="text-mainColor">
            Total cart price : {cartDetails.totalCartPrice}
          </h6>
          {cartDetails.products.map((product) => (
            <div
              className="row border-bottom py-2 my-2 align-items-center"
              key={product.product._id}
            >
              <div className="col-md-1">
                <img
                  src={product.product.imageCover}
                  className="w-100"
                  alt=""
                />
              </div>
              <div className="col-md-11 d-flex justify-content-between">
                <div>
                  <h6>{product.product.title}</h6>
                  <h6 className="text-mainColor">Price:{product.price}</h6>
                  <button
                    className=" btn m-0 p-0 "
                    onClick={() => deleteItem(product.product._id)}
                  >
                    <i className="fa-regular fa-trash-can text-mainColor"></i>{" "}
                    Remove
                  </button>
                </div>
                <div>
                  <button
                    onClick={() =>
                      updateItemCount(product.product._id, product.count - 1)
                    }
                    className="btn border-main btn-sm"
                  >
                    -
                  </button>
                  <span className="mx-2">{product.count}</span>
                  <button
                    onClick={() =>
                      updateItemCount(product.product._id, product.count + 1)
                    }
                    className="btn border-main btn-sm"
                  >
                    +
                  </button>
                </div>
              </div>
            </div>
          ))}
          <button className="btn bg-main">
            <Link className="text-white" to={"/shoppingCart/checkout"}>
              Checkout
            </Link>
          </button>
        </div>
      ) : null}
    </>
  );
}
